@mixin clearfix {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

@mixin respond($breakpoint) {
  @if $breakpoint == tab-land {
    @media (max-width: 75em) {  //1200px
      @content;
    }
  }
  @if $breakpoint == tab-port {
    @media (max-width: 56.25em) { //900px
      @content;
    }
  }
  @if $breakpoint == phone {
    @media (max-width: 37.5em) { //900px
      @content;
    }
  }
  @if $breakpoint == big {
    @media (min-width: 112.5em) { //1800px
      @content;
    }
  }
}
